<template>
  <!--  -->
  <div class="user">
    <h4></h4>
    <!--  -->
    <div class="screen">
      <el-row :gutter="10">
        <el-col :span="14">
          <span></span>
          <el-input size="small" v-model="nickname" placeholder="" :clearable="true"></el-input>
          <span style="margin-left: 2%;"></span>
          <el-input size="small" v-model="phone" placeholder="" :clearable="true"></el-input>
          <el-button size="small" @click="queryUserInfo()" type="primary"></el-button>
        </el-col>
      </el-row>
    </div>
    <!--  -->
    <el-row :gutter="70" style="margin-top: 0.3%;">
      <el-col :span="1" v-if="isAdmin"><el-button type="success" @click="dialogVisible = true" size="mini" plain></el-button></el-col>
      <el-col :span="1"><el-button type="danger" @click="deleteUser()" size="mini" plain></el-button></el-col>
      <el-col :span="10"  v-if="isAdmin">
        <el-switch style="display: block;margin-top: 1%;" v-model="switchUser" active-color="#13ce66" inactive-color="#ff4949" active-text="" inactive-text=""></el-switch>
      </el-col>
    </el-row>
    <!--  -->
    <div class="data">
      <el-table
        :data="switchUser ? adminInfo : userInfo"
        style="width: 100%;"
        @selection-change="handleSelectionChange"
        max-height="500">
        <el-table-column
          type="selection"
          width="55">
          </el-table-column>
        <el-table-column
          prop="name"
          :label="switchUser ? '' : ''"
          width="200">
        </el-table-column>
        <el-table-column v-if="switchUser"
          prop="role"
          label=""
          width="180">
          <template slot-scope="scope">
            {{ scope.row.role == 0 ? '' : '' }}
          </template>
        </el-table-column>
        <el-table-column
          prop="phone"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="state"
          label=""
          width="180"
          :filters="[{ text: '', value: true }, { text: '', value: false }]"
          :filter-method="filterTag"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.state ? 'success' : 'danger'"
              disable-transitions>{{scope.row.state ? '' : ''}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="createTime"
          label=""
          sortable
          width="180">
        </el-table-column>
        <el-table-column
          label=""
          width="180">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.state" v-if="scope.row.role != 1" @change="disableUser(scope.row.userId, scope.row.state)" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <el-dialog
      :append-to-body="true"
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <el-row>
        <el-col :span="3"></el-col>
        <el-col :span="21" style="display: flex;">
          <el-input size="small" v-model="addAdminUser.account" placeholder="" :clearable="true"></el-input>
        </el-col>
        <el-col style="margin-top: 1%;" :span="3"></el-col>
        <el-col style="margin-top: 1%;" :span="21">
         <el-input size="small" v-model="addAdminUser.password" placeholder="" :clearable="true"></el-input>
        </el-col>
        <el-col style="margin-top: 1%;" :span="3"></el-col>
        <el-col style="margin-top: 1%;" :span="21">
          <el-input size="small" v-model="addAdminUser.phone" placeholder="" :clearable="true"></el-input>
        </el-col>
      </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false"> </el-button>
        <el-button size="small" type="primary" @click="addAdminUseInfo()"> </el-button>
      </span>
    </el-dialog>
    <span style="position: fixed;bottom: 2%;margin-left: 18%;color: #43b597;font-family: Arial;font-size: 12px;letter-spacing: 2px;">Design and Implementation of a Furniture Store System Based on WeChat Mini Program.</span>
  </div>
</template>

<script>
import { Message } from 'element-ui';
import { getUserInfo, addAdmin, delUser, updateUserState } from '../../request/api';
export default {
  name: 'User',
  data () {
    return {
      // 
      phone: '',
      // 
      nickname: '',
      // ID
      userIds: [],
      // 
      addAdminUser: {
        account: '',
        password: '',
        phone: '',
      },
      // 
      isAdmin: true,
      // 
      switchUser: false,
      // 
      dialogVisible: false,
      userInfo: [],
      adminInfo: []
    }
  },
  async mounted () {
    // 
    let queryVO = {};
    const res = await getUserInfo(0, queryVO);
    if(res.code === 200) {
      res.data.forEach(element => {
        if(element.sysType === 1) {
          this.adminInfo = element.userInfoBOS;
        } else {
          this.userInfo = element.userInfoBOS;
        }
      })
    }
  },
  methods: {
    // 
    async queryUserInfo() {
      let res;
      // 
      if(this.nickname == '' && this.phone == '') {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        // 
        let queryVO = {};
        res = await getUserInfo(0, queryVO);
      }
      else if(this.nickname != '') {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        let queryVO = {};
        queryVO.filed = this.switchUser ? 'account' : 'nickname';
        queryVO.value = this.nickname;
        res = await getUserInfo(this.switchUser ? 1 : 2, queryVO);
      } else {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        let queryVO = {};
        queryVO.filed = 'phone';
        queryVO.value = this.phone;
        res = await getUserInfo(this.switchUser ? 1 : 2, queryVO);
      }
      if(res.code === 200) {
        res.data.forEach(element => {
          if(element.sysType === 1) {
            this.adminInfo = element.userInfoBOS;
          } else {
            this.userInfo = element.userInfoBOS;
          }
        })
      } else {
        Message({
          message: res.msg,
          type: 'error',
          duration: 2000
        });
      }
    },
    // 
    async deleteUser() {
      if(!this.switchUser) {
        return Message({
          message: '',
          type: 'error',
          duration: 2000
        });
      }
      if(this.userIds.length == 0) {
        return Message({
          message: '',
          type: 'error',
          duration: 2000
        });
      }
      const res = await delUser(this.userIds);
      if(res.code !== 200) {
        return Message({
          message: res.msg,
          type: 'error',
          duration: 2000
        });
      }
      Message({
        message: '',
        type: 'success',
        duration: 2000
      });
      // 
      let queryVO = {};
      const user = await getUserInfo(0, queryVO);
      if(user.code === 200) {
        user.data.forEach(element => {
          if(element.sysType === 1) {
            this.adminInfo = element.userInfoBOS;
          } else {
            this.userInfo = element.userInfoBOS;
          }
        })
      }
    },

    // 
    async disableUser(userId, state) {
      const res = await updateUserState([userId, state, this.switchUser]);
      if(res.code !== 200) {
        return Message({
          message: res.msg,
          type: 'error',
          duration: 2000
        });
      }
      Message({
        message: '',
        type: 'success',
        duration: 2000
      });
      // 
      let queryVO = {};
      const user = await getUserInfo(0, queryVO);
      if(user.code === 200) {
        user.data.forEach(element => {
          if(element.sysType === 1) {
            this.adminInfo = element.userInfoBOS;
          } else {
            this.userInfo = element.userInfoBOS;
          }
        })
      }
    },
    // 
    async addAdminUseInfo() {
      if(this.addAdminUser.account == '' || this.addAdminUser.password == '' || this.addAdminUser.phone == '') {
        return Message({
          message: '',
          type: 'error',
          duration: 2000
        });
      }
      const res = await addAdmin(this.addAdminUser);
      if(res.code !== 200) {
        return Message({
          message: res.msg,
          type: 'error',
          duration: 2000
        });
      }
      Message({
        message: '',
        type: 'success',
        duration: 2000
      });
      this.dialogVisible = false;
      // 
      let queryVO = {};
      const user = await getUserInfo(0, queryVO);
      if(user.code === 200) {
        user.data.forEach(element => {
          if(element.sysType === 1) {
            this.adminInfo = element.userInfoBOS;
          } else {
            this.userInfo = element.userInfoBOS;
          }
        })
      }
    },
    // 
    handleSelectionChange(val) {
      this.userIds = [];
      val.forEach(element => {
        this.userIds.push(element.userId)
      });
    },
    filterTag(value, row) {
      return row.state === value;
    },
  }
}
</script>

<style scoped>
  .user {
    width: 94%;
    margin: 1%;
    padding: 1% 2% 2% 2%;
    border-radius: 3px;
    background: white;
  }
  .screen>div>div {
    display: flex;
    line-height: 50px;
  }
  .screen>div>div .el-input, .screen>div>div .el-select {
    width: 30%;
    margin-left: 2%;
  }
  .screen>div>div .el-button {
    width: 70px;
    height: 31px;
    margin-left: 1%;
    margin-top: 1.2%;
  }
</style>


