<template>
  <!-- / -->
  <div class="addProduct">
    <el-row>
      <el-col :span="12">
        <h4>/</h4>
      </el-col>
      <el-col :span="12" style="text-align: right;padding-right: 3%;">
        <el-button style="width: 20%;" @click="saveProduct()" type="success"></el-button>
      </el-col>
    </el-row>
    <!--  -->    
    <div class="info">
      <el-row :gutter="10">
        <el-col :span="6">
          <span></span>
          <el-input v-model="product.name" placeholder=""></el-input>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-input v-model="product.identifier" placeholder=""></el-input>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-input v-model="product.producer" placeholder=""></el-input>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-autocomplete class="inline-input" v-model="product.materialQuality" :fetch-suggestions="materialQualitySearch" placeholder=""></el-autocomplete>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-autocomplete class="inline-input" v-model="product.style" :fetch-suggestions="styleSearch" placeholder=""></el-autocomplete>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-autocomplete class="inline-input" v-model="product.category" :fetch-suggestions="categorySearch" placeholder=""></el-autocomplete>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-autocomplete class="inline-input" v-model="product.space" :fetch-suggestions="spaceSearch" placeholder=""></el-autocomplete>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-autocomplete class="inline-input" v-model="product.filler" :fetch-suggestions="fillerSearch" placeholder=""></el-autocomplete>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-input v-model="product.price" placeholder=""></el-input>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-input-number v-model="product.inventory" :min="1" label=""></el-input-number>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-input v-model="product.size" placeholder=""></el-input>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-input v-model="product.colour" placeholder=""></el-input>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-input v-model="product.materialType" placeholder=""></el-input>
        </el-col>
        <el-col :span="18">
          <span></span>
          <el-input style="width: 87%;margin-left: 0.7%;" type="textarea" :rows="2" v-model="product.introduce" placeholder=""></el-input>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-upload
            class="upload-demo"
            action="#"
            ref="upload"
            :multiple="true"
            :on-change="imageChange"
            :file-list="product.images"
            :auto-upload="false">
            <el-button slot="trigger" @click="openMessage('')" size="max" type="primary"><i class="el-icon-upload el-icon--right"></i></el-button>
          </el-upload>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-upload
            class="upload-demo"
            action="#"
            ref="upload"
            :multiple="true"
            :on-change="carouselImagesChange"
            :file-list="product.carouselImages"
            :auto-upload="false">
            <el-button slot="trigger" @click="openMessage('')" size="max" type="warning"><i class="el-icon-upload el-icon--right"></i></el-button>
          </el-upload>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-upload
            class="upload-demo"
            action="#"
            ref="upload"
            :multiple="true"
            :on-change="physicalImagesChange"
            :file-list="product.physicalImages"
            :auto-upload="false">
            <el-button slot="trigger" @click="openMessage('5')" size="max" type="info"><i class="el-icon-upload el-icon--right"></i></el-button>
          </el-upload>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-upload
            class="upload-demo"
            action="#"
            ref="upload"
            :multiple="true"
            :on-change="detailsImagesChange"
            :file-list="product.detailsImages"
            :auto-upload="false">
            <el-button slot="trigger" @click="openMessage('10')" size="max" plain type="info"><i class="el-icon-upload el-icon--right"></i></el-button>
          </el-upload>
        </el-col>
      </el-row>
    </div>
    <span style="position: fixed;bottom: 2%;margin-left: 18%;color: #43b597;font-family: Arial;font-size: 12px;letter-spacing: 2px;">Design and Implementation of a Furniture Store System Based on WeChat Mini Program.</span>
  </div>
</template>

<script>
import { Message } from 'element-ui';
import { addProductInfo, getProductById } from '../../request/api';
export default {
  name: 'AddProduct',
  data () {
    return {
      // ID
      productId: '',
      // 
      imageSize: -1,
      // 
      product: {
        name: '',
        introduce: '',
        identifier: '',
        producer: '',
        materialQuality: '',
        filler: '',
        style: '',
        category: '',
        space: '',
        price: '',
        inventory: '',
        size: '',
        colour: '',
        materialType: '',
        images: [],
        carouselImages: [],
        physicalImages: [],
        detailsImages: [],
      },
      // 
      materialQualityRes: [{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" }],
      // 
      styleRes: [{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "/" },{ value: "" }],
      // 
      categoryRes: [{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" }],
      // 
      spaceRes: [{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" }],
      // 
      fillerRes: [{ value: "" },{ value: "+" },{ value: "" },{ value: "" }],
    }
  },
  async mounted () {
    // urlID
    this.productId = this.$route.query.id;
    if(typeof this.productId != 'undefined') {
      const res = await getProductById([this.productId]);
      if (res.code != 200) {
        this.productId = '';
        return Message({
          message: res.message,
          type: 'error',
          duration: 2000
        });
      }
      this.product = res.data;
      this.product.images = [];
      this.product.carouselImages = [];
      this.product.physicalImages = [];
      this.product.detailsImages = [];
      this.imageSize = this.product.size.split(" ").length;
    }
  },
  methods: {
    // /
    async saveProduct() {
      if (this.productId != '') {
        if(this.imageSize != this.product.size.split(" ").length) {
          if(this.product.images.length != this.product.size.split(" ").length) {
            return Message({
              message: ' ',
              type: 'error',
              duration: 3000
            });
          }
        }
      }
      let formData = new FormData();
      for (const file of this.product.images) {  //files
        if(file) {
          formData.append('images', file);
        }
      }
      for (const file of this.product.carouselImages) {  //files
        if(file) {
          formData.append('carouselImages', file);
        }
      }
      for (const file of this.product.physicalImages) {  //files
        if(file) {
          formData.append('physicalImages', file);
        }
      }
      for (const file of this.product.detailsImages) {  //files
        if(file) {
          formData.append('detailsImages', file);
        }
      }
      formData.append('productId', this.productId);
      formData.append('name', this.product.name);
      formData.append('introduce', this.product.introduce);
      formData.append('identifier', this.product.identifier);
      formData.append('producer', this.product.producer);
      formData.append('materialQuality', this.product.materialQuality);
      formData.append('filler', this.product.filler);
      formData.append('style', this.product.style);
      formData.append('category', this.product.category);
      formData.append('space', this.product.space);
      formData.append('price', this.product.price);
      formData.append('inventory', this.product.inventory);
      formData.append('size', this.product.size);
      formData.append('colour', this.product.colour);
      formData.append('materialType', this.product.materialType);
      formData.append('opt', typeof this.productId == 'undefined' ? true : false);

      const res = await addProductInfo(formData);
      if(res.code == 200) {
        this.product = { name: '', introduce: '', identifier: '', producer: '', materialQuality: '', filler: '', style: '', category: '',
          space: '', price: '', inventory: '', size: '', colour: '', materialType: '', images: [], carouselImages: [], physicalImages: [],detailsImages: []};
        Message({
          message: '',
          type: 'success',
          duration: 2000
        });
        if (this.productId != '') {
          return this.$router.push({name: 'product-info'})
        }
      }
    },
    // 
    imageChange(file, fileList) {
      this.product.images = [];
      fileList.forEach(element => {
        this.product.images.push(element.raw)
      });
    },
    carouselImagesChange(file, fileList) {
      this.product.carouselImages = [];
      fileList.forEach(element => {
        this.product.carouselImages.push(element.raw)
      });
    },
    physicalImagesChange(file, fileList) {
      this.product.physicalImages = [];
      fileList.forEach(element => {
        this.product.physicalImages.push(element.raw)
      });
    },
    detailsImagesChange(file, fileList) {
      this.product.detailsImages = [];
      fileList.forEach(element => {
        this.product.detailsImages.push(element.raw)
      });
    },
    openMessage(message) {
      this.$message({
        type: 'warning',
        showClose: true,
        duration: 10000,
        message: message,
      });
    },
    materialQualitySearch(queryString, cb) {
      var restaurants = this.materialQualityRes;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    styleSearch(queryString, cb) {
      var restaurants = this.styleRes;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    categorySearch(queryString, cb) {
      var restaurants = this.categoryRes;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    spaceSearch(queryString, cb) {
      var restaurants = this.spaceRes;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    fillerSearch(queryString, cb) {
      var restaurants = this.fillerRes;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    }
  }
}
</script>

<style scoped>
  .addProduct {
    width: 94%;
    margin: 1%;
    padding: 1% 2% 2% 2%;
    border-radius: 3px;
    background: white;
  }
  .info {
    padding: 1% 0 5% 0;
  }
  .info>div>div {
    display: flex;
    line-height: 60px;
    margin-top: 1%;
  }
  .info>div>div .el-input, .info>div>div .el-autocomplete, .info>div>div .el-input-number {
    width: 60%;
    margin-left: 2%;
  }
  .info>div>div .el-input-number {
    margin-top: 3%;
  }
  .info>div>div .el-button {
    width: 160%;
    margin-left: 7%;
  }
</style>


