<template>
  <!--  -->
  <div class="rule">
    <h4></h4>
    <!-- Token -->
    <div class="screen">
      <el-row :gutter="10">
        <el-col :span="24">
          <span>accessToken</span>
          <el-input size="small" v-model="accessToken" placeholder="accessToken"></el-input>
          <span style="margin-left: 2%;">refreshToken</span>
          <el-input size="small" v-model="refreshToken" placeholder="refreshToken"></el-input>
          <el-button size="small" @click="saveToken()" type="primary"></el-button>
        </el-col>
      </el-row>
    </div>
    <h4></h4>
    <!--  -->
    <div class="screen">
      <el-row :gutter="10">
        <el-col :span="19">
          <span></span>
          <el-input size="small" v-model="info.title" placeholder="" :clearable="true"></el-input>
          <span style="margin-left: 2%;"></span>
          <el-autocomplete style="margin-left: 1%;margin-top: -0.2%;" class="inline-input" size="small" v-model="info.category" :fetch-suggestions="categorySearch" placeholder="" :clearable="true"></el-autocomplete>
          <span style="margin-left: 2%;"></span>
          <el-select v-model="info.showType" size="small" placeholder="">
            <el-option v-for="item in [{label: '', value: true}, {label: '', value: false}]" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
          <el-button size="small" @click="saveInfo()" type="success"></el-button>
        </el-col>
      </el-row>
    </div>
    <!--  -->
    <div class="data">
      <el-table
        :data="indexData"
        style="width: 100%;"
        max-height="500">
        <el-table-column
          prop="title"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="category"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="showType"
          label=""
          width="200"
          :filters="[{ text: '', value: true }, { text: '', value: false }]"
          :filter-method="filterTag"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.showType ? 'success' : 'warning'"
              disable-transitions>{{scope.row.showType ? '' : ''}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="180">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <span style="position: fixed;bottom: 2%;margin-left: 18%;color: #43b597;font-family: Arial;font-size: 12px;letter-spacing: 2px;">Design and Implementation of a Furniture Store System Based on WeChat Mini Program.</span>
  </div>
</template>

<script>
import { Message } from 'element-ui';
import { setTokenExpireTime, getWeChatHomeDataCategory, saveWeChatHomeDataCategory } from '../../request/api';
export default {
  name: 'RuleData',
  data () {
    return {
      accessToken: '',
      refreshToken: '',
      // 
      info: {title: '', category: '', showType: ''},
      categoryRes: [{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" },{ value: "" }],
      indexData: [],
    }
  },
  async mounted () {
    // 
    const res = await getWeChatHomeDataCategory();
    if(res.code == 200) {
      this.indexData = res.data;
    }
  },
  methods: {
    // Token
    async saveToken() {
      if(this.accessToken == '' || this.refreshToken == '') {
        return Message({
          message: '',
          type: 'error',
          duration: 2000
        });
      }
      const res = await setTokenExpireTime([this.accessToken, this.refreshToken]);
      if(res.code == 200) {
        this.accessToken = this.refreshToken = '';
        return Message({
          message: '',
          type: 'success',
          duration: 2000
        });
      }
      Message({
        message: '',
        type: 'error',
        duration: 2000
      });
    },
    // 
    async saveInfo() {
      console.log(this.info.showType);
      console.log(this.info.title);
      console.log(this.info.category);
      if(this.info.title == '' || this.info.category == '' || this.info.showType === '') {
        return Message({
          message: '',
          type: 'error',
          duration: 2000
        });
      }
      this.indexData.push(this.info);
      const res = await saveWeChatHomeDataCategory(this.indexData);
      if(res.code == 200) {
        // 
        const response = await getWeChatHomeDataCategory();
        if(response.code == 200) {
          this.indexData = response.data;
        }
        return Message({
          message: '',
          type: 'success',
          duration: 2000
        });
      }
      Message({
        message: '',
        type: 'error',
        duration: 2000
      });
    },
    // 
    async handleDelete(index, row) {
      const filteredArray = this.indexData.filter(item => item !== row);
      const res = await saveWeChatHomeDataCategory(filteredArray);
      if(res.code == 200) {
        // 
        const response = await getWeChatHomeDataCategory();
        if(response.code == 200) {
          this.indexData = response.data;
        }
        return Message({
          message: '',
          type: 'success',
          duration: 2000
        });
      }
      Message({
        message: '',
        type: 'error',
        duration: 2000
      });
    },
    filterTag(value, row) {
      return row.showType === value;
    },
    categorySearch(queryString, cb) {
      var restaurants = this.categoryRes;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
  }
}
</script>

<style scoped>
  .rule {
    width: 94%;
    margin: 1%;
    padding: 1% 2% 2% 2%;
    border-radius: 3px;
    background: white;
  }
  .screen>div>div {
    display: flex;
    line-height: 50px;
  }
  .screen>div>div .el-input, .screen>div>div .el-select {
    width: 20%;
    margin-left: 2%;
  }
  .screen>div>div .el-button {
    width: 70px;
    height: 31px;
    margin-left: 1%;
    margin-top: 0.8%;
  }
</style>


