<!--
 * @Author:  address
 * @Date: 2024-03-07 11:33:57
 * @LastEditors:  address
 * @LastEditTime: 2024-03-13 15:43:09
 * @FilePath: \managePlatform\src\components\product\ProductInfo.vue
 * @Description: ,`customMade`, koroFileHeader : https://github.com/OBKoro1/koro1FileHeader/wiki/%E9%85%8D%E7%BD%AE
-->
<template>
  <!--  -->
  <div class="productInfo">
    <h4></h4>
    <!--  -->
    <div class="screen">
      <el-row :gutter="10">
        <el-col :span="14">
          <span></span>
          <el-input size="small" v-model="identifier" placeholder="" :clearable="true"></el-input>
          <span style="margin-left: 2%;"></span>
          <el-input size="small" v-model="name" placeholder="" :clearable="true"></el-input>
          <el-button size="small" @click="queryProduct()" type="primary"></el-button>
          <el-button size="small" @click="deleteProduct()" type="danger"></el-button>
        </el-col>
      </el-row>
    </div>
    <!--  -->
    <div class="data">
      <el-table
        :data="productInfo"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        max-height="500">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          fixed
          prop="image"
          label=""
          width="160">
          <template slot-scope="scope">
            <img :src="scope.row.image" style="width: 60%;" />
          </template>
        </el-table-column>
        <el-table-column
          prop="identifier"
          label=""
          width="170">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          :show-overflow-tooltip="true"
          width="170">
        </el-table-column>
        <el-table-column
          prop="category"
          label=""
          :filters="categoryRes"
          :filter-method="categoryResFilterHandler"
          filter-placement="bottom-end"
          width="80">
        </el-table-column>
        <el-table-column
          prop="space"
          label=""
          :filters="spaceRes"
          :filter-method="spaceResFilterHandler"
          filter-placement="bottom-end"
          width="80">
        </el-table-column>
        <el-table-column
          prop="style"
          label=""
          :filters="styleRes"
          :filter-method="styleResFilterHandler"
          filter-placement="bottom-end"
          width="100">
        </el-table-column>
        <el-table-column
          prop="price"
          label=""
          sortable
          width="110">
          <template slot-scope="scope">
            {{ scope.row.price }}
          </template>
        </el-table-column>
        <el-table-column
          prop="sold"
          label=""
          sortable
          width="100">
          <template slot-scope="scope">
            {{ scope.row.sold }}
          </template>
        </el-table-column>
        <el-table-column
          prop="inventory"
          label=""
          sortable
          width="100">
          <template slot-scope="scope">
            {{ scope.row.inventory }}
          </template>
        </el-table-column>
        <el-table-column
          prop="materialQuality"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="filler"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="heat"
          label=""
          sortable
          width="100">
        </el-table-column>
        <el-table-column
          prop="score"
          label=""
          sortable
          width="100">
          <template slot-scope="scope">
            {{ scope.row.score }}
          </template>
        </el-table-column>
        <el-table-column
          prop="creationTime"
          label=""
          sortable
          :show-overflow-tooltip="true"
          width="115">
        </el-table-column>
        <el-table-column
          fixed="right"
          label=""
          width="110">
          <template slot-scope="scope">
            <el-button @click.native.prevent="updateProduct(scope.$index, productInfo)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        layout="prev, pager, next"
        @prev-click="prevInfo"
        @next-click="nextInfo"
        :total="total">
      </el-pagination>
    </div>
    <span style="position: fixed;bottom: 2%;margin-left: 18%;color: #43b597;font-family: Arial;font-size: 12px;letter-spacing: 2px;">Design and Implementation of a Furniture Store System Based on WeChat Mini Program.</span>
  </div>
</template>

<script>
import { Message } from 'element-ui';
import { getProductRecord, delProductInfo } from '../../request/api';
export default {
  name: 'ProductInfo',
  data () {
    return {
      // 
      identifier: '',
      // 
      name: '',
      // 
      total: 0,
      // 
      page: 1,
      // ID
      productId: [],
      // 
      productInfo: [],
      // 
      styleRes: [{ text: "", value: "" },{text: "",  value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "/", value: "/" },{ text: "", value: "" }],
      // 
      categoryRes: [{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" }],
      // 
      spaceRes: [{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" },{ text: "", value: "" }],

    }
  },
  async mounted () {
    // 
    let queryVO = {};
    queryVO.filed = null;
    queryVO.value = null;
    queryVO.page = 1;
    const res = await getProductRecord(queryVO);
    if(res.code == 200) {
      this.productInfo = res.data.productRecordBOS;
      this.total = res.data.total;
      this.page = res.data.page;
    }

  },
  methods: {
    // 
    async queryProduct() {
      var res;
      // 
      let queryVO = {};
      queryVO.filed = null;
      queryVO.value = null;
      queryVO.page = 1;
      // 
      if(this.identifier == '' && this.name == '') {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        res = await getProductRecord(queryVO);
      } else if(this.identifier != '') {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        queryVO.filed = "identifier";
        queryVO.value = this.identifier;
        res = await getProductRecord(queryVO);
      } else {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        queryVO.filed = "name";
        queryVO.value = this.name;
        res = await getProductRecord(queryVO);
      }
      if(res.code == 200) {
        this.productInfo = res.data.productRecordBOS;
        this.total = res.data.total;
        this.page = res.data.page;
      }
    },
    // 
    async deleteProduct() {
      if(this.productId.length == 0) {
        return Message({
          message: '',
          type: 'error',
          duration: 2000
        });
      }
      const res = await delProductInfo(this.productId);
      if(res.code == 200) {
        Message({
          message: '',
          type: 'success',
          duration: 2000
        });
        // 
        let queryVO = {};
        queryVO.filed = null;
        queryVO.value = null;
        queryVO.page = 1;
        const res = await getProductRecord(queryVO);
        if(res.code == 200) {
          this.productInfo = res.data.productRecordBOS;
          this.total = res.data.total;
          this.page = res.data.page;
        }
      }
    },
    // 
    async prevInfo(index) {
      var res;
      // 
      let queryVO = {};
      queryVO.filed = null;
      queryVO.value = null;
      queryVO.page = index;
      // 
      if(this.identifier == '' && this.name == '') {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        res = await getProductRecord(queryVO);
      } else if(this.identifier != '') {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        queryVO.filed = "identifier";
        queryVO.value = this.identifier;
        res = await getProductRecord(queryVO);
      } else {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        queryVO.filed = "name";
        queryVO.value = this.name;
        res = await getProductRecord(queryVO);
      }
      if(res.code == 200) {
        this.productInfo = res.data.productRecordBOS;
        this.total = res.data.total;
        this.page = res.data.page;
      }
    },
    // 
    async nextInfo(index) {
      var res;
      // 
      let queryVO = {};
      queryVO.filed = null;
      queryVO.value = null;
      queryVO.page = index;
      // 
      if(this.identifier == '' && this.name == '') {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        res = await getProductRecord(queryVO);
      } else if(this.identifier != '') {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        queryVO.filed = "identifier";
        queryVO.value = this.identifier;
        res = await getProductRecord(queryVO);
      } else {
        Message({
          message: '',
          type: 'info',
          duration: 2000
        });
        queryVO.filed = "name";
        queryVO.value = this.name;
        res = await getProductRecord(queryVO);
      }
      if(res.code == 200) {
        this.productInfo = res.data.productRecordBOS;
        this.total = res.data.total;
        this.page = res.data.page;
      }
    },
    // 
    updateProduct(index, productInfo) {
      const productId = productInfo[index].productId;
      this.$router.push({
        name: 'add-product',
        query: {
          id: productId
        }
      })
    },
    // 
    handleSelectionChange(val) {
      this.productId = [];
      val.forEach(element => {
        this.productId.push(element.productId)
      });
    },

    styleResFilterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },
    categoryResFilterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },
    spaceResFilterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    }
  }
}
</script>

<style scoped>
  .productInfo {
    width: 94%;
    border-radius: 3px;
    margin: 1%;
    padding: 1% 2% 2% 2%;
    background: white;
  }
  .screen>div>div {
    display: flex;
    line-height: 50px;
  }
  .screen>div>div .el-input {
    width: 30%;
    margin-left: 2%;
  }
  .screen>div>div .el-button {
    width: 70px;
    height: 31px;
    margin-left: 1%;
    margin-top: 1.2%;
  }
  .data {
    margin-top: 1%;
  }
  .el-pagination {
    text-align: right;
    margin-top: 0.5%;
  }
</style>


